<template>
  <div class="home">
    <form action="#" @submit.prevent="login" class="flex-column">
      <input v-model="email" type="text" class="input mb-8" placeholder="E-mail">
      <input v-model="password" type="text" class="input mb-8" placeholder="Password">
      <button type="submit" class="btn btn-primary">Login</button>
    </form>

    <router-view/>
  </div>
</template>

<script>
  import axios from 'axios'
  
  axios.defaults.withCredentials = true
  axios.defaults.baseURL = 'http://localhost:8000'

  export default {
    name: 'Home',

    data() {
      return {
        email: '',
        password: ''
      }
    },

    methods: {
      login() {
        axios.get('/sanctum/csrf-cookie').then((response) => {
          axios.post('/login', {
            email: this.email,
            password: this.password
          })
        })
      }
    }
  }
</script>
