<template>
  <div class="flex-column flex-ai-center">
    <h1>Dashboard</h1>
    <p>Your email: {{ email }}</p>
    <br>
    <br>
    <input v-model="request.url" type="text" class="input" placeholder="Url">
    <input v-model="request.method" type="text" class="input mt-8" placeholder="Method">
    <button @click="sendRequest" class="btn btn-primary mt-8">Send request</button>
    <br>
    <input type="file" @change="onFileChanged">
    <button @click="sendFile" class="btn btn-primary mt-8">Send file</button>
  </div>
</template>

<script>
  import axios from 'axios'
  
  axios.defaults.withCredentials = true
  axios.defaults.baseURL = 'http://localhost:8000'

  export default {
    name: 'Dashboard',

    data() {
      return {
        email: '',
        request: {
          url: '',
          method: ''
        },
        file: null
      }
    },

    methods: {
      sendRequest() {
        axios({
          method: this.request.method.toLowerCase(),
          url: this.request.url
        })
        .then(res => console.log(res))
      },

      sendFile() {
        let data = new FormData()
        data.append('file', this.file)

        axios.post(this.request.url, data, {
          onUploadProgress(pe) {
            let percent = Math.round((pe.loaded * 100) / pe.total)
            console.log(`${percent}% Uploading ...`)
          }
        })
        .then(res => {
          console.log('File was uploaded', res)
        })
        .catch(err => {
          console.error('There was an error while uploading', err)
        })
      },

      onFileChanged(event) {
        if (!!event.target.files.length) {
          this.file = event.target.files[0]
        } else {
          this.file = null
        }
      }
    },

    mounted() {
      axios.get('/api/user')
        .then(response => {
          this.email = response.data.email
        })
        .catch(err => { console.error(err) })
    }
  }
</script>
